import { card_infos} from './infos';

//  seed cardModel 
const seedAttack1 = {
  name: '',
  desc: '',
  attack: 10,
  armor: 0,
  positive: false,
  extraInfo: card_infos.attack[0],
};

const seedAttack2 = {
  name: '',
  desc: '',
  attack: 1,
  armor: 0,
  positive: false,
  extraInfo: card_infos.attack[1],
};

const seedDefend1 = {
  name: '',
  desc: '',
  attack: 0,
  armor: 1,
  positive: true,
  extraInfo: card_infos.defend[0],
};

const seedDefend2 = {
  name: '',
  desc: '',
  attack: 0,
  armor: 5,
  positive: true,
  extraInfo: card_infos.defend[0],
};

const config = [
  {
    seed: seedAttack1,
    num: 3,
  },
  {
    seed: seedAttack2,
    num: 3,
  },
  {
    seed: seedDefend1,
    num: 2,
  },
  {
    seed: seedDefend2,
    num: 2,
  },
]

//  seed  seed 
function generateSeeds(config) {
  const result = [];

  config.forEach(s => {
    for (let i = 0; i < s.num; i++) {
      result.push(s.seed);
    }
  })

  return result;
}

const heroSeeds = generateSeeds(config);
const monsterSeeds = generateSeeds(config);

export default {
  heroSeeds,
  monsterSeeds,
};


